import React from 'react';
import { Row, Col, Table, Icon, Input } from 'antd';
import Card from '../../components/common/Card/index';
import CardTitle from '../../components/common/CardTitle/index';
import './styles.scss';

const columns = [
  {
    title: 'Subjects',
    dataIndex: 'name',
    sorter: (a, b) => a.name.length - b.name.length,
    sortDirections: ['descend'],
  },
  {
    title: 'Mid Test',
    dataIndex: 'midTest',
    defaultSortOrder: 'descend',
    className: 'col-midTest',
    sorter: (a, b) => a.age - b.age,
  },
  {
    title: 'Final Test',
    dataIndex: 'finalTest',
    defaultSortOrder: 'descend',
    className: 'col-finalTest',
    sorter: (a, b) => a.age - b.age,
  },
  {
    title: 'Total',
    dataIndex: 'total',
    className: 'col-total',
    defaultSortOrder: 'descend',
    sorter: (a, b) => a.age - b.age,
  },
];

const data = [
  {
    key: '1',
    name: 'Englist',
    midTest: 32,
    finalTest: 82,
    total: 52,
  },
  {
    key: '2',
    name: 'Maths',
    midTest: 32,
    finalTest: 42,
    total: 36,
  },
  {
    key: '1',
    name: 'Englist',
    midTest: 32,
    finalTest: 82,
    total: 52,
  },
  {
    key: '2',
    name: 'Maths',
    midTest: 32,
    finalTest: 42,
    total: 36,
  },
  {
    key: '2',
    name: 'Maths',
    midTest: 32,
    finalTest: 42,
    total: 36,
  },
  {
    key: '1',
    name: 'Englist',
    midTest: 32,
    finalTest: 82,
    total: 52,
  },
  {
    key: '2',
    name: 'Maths',
    midTest: 32,
    finalTest: 42,
    total: 36,
  },
  {
    key: '2',
    name: 'Maths',
    midTest: 32,
    finalTest: 42,
    total: 36,
  },
];

function onChange() {
}

const { Search } = Input;

export default () => {
  return (
    <Row>
      <Col span={24}>
        <Card>
          <CardTitle title="School Results" />
          <Search className="input-search" />
          <div style={{ textAlign: 'right' }}>
            <button className="btn btn-download">
              <Icon type="download" /> Download
            </button>
            <button className="btn btn-print">
              <Icon type="printer" /> Print
            </button>
          </div>
         
          <Table pagination={false} columns={columns} dataSource={data} onChange={onChange} />
        </Card>
      </Col>
    </Row>
  );
};



